						<text class="info-label">ÁîµË°®Âè∑Á†ÅÔºö</text>
						<text class="info-value">{{ meterInfo.meter_num||'Á≠âÂæÖÊü•ËØ¢' }}</text>
					</view>
					<view class="info-item">
