						<text class="info-value">{{ meterInfo.meter_company||'Á≠âÂæÖÊü•ËØ¢' }}</text>
					</view>
					<view class="info-item">
						<text class="info-label">ÁîµË°®‰ΩçÁΩÆÔºö</text>
						<text class="info-value">{{ meterInfo.meter_place||'Á≠âÂæÖÊü•ËØ¢' }}</text>
					</view>
				</view>
			</view>

			<!-- ÂÖÖÂÄºÈáëÈ¢ùËæìÂÖ• -->
			<view class="input-card">
