					<view class="input-box">
						<text class="currency-symbol">Ôø•</text>
						<input type="digit" 
							v-model="amount" 
							placeholder="ËØ∑ËæìÂÖ•ÂÖÖÂÄºÈáëÈ¢ù" />
					</view>
				</view>
			</view>

			<!-- ÂÖÖÂÄºÊåâÈíÆ -->
			<view class="operation-section">
				<button class="action-btn charge" @click="startScan">
					<text class="btn-icon">üí∞</text>
					Á´ãÂç≥ÂÖÖÂÄº
				</button>
			</view>

			<!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
			<view class="footer-actions">
				<view class="footer-btn" @click="getorder">
					<text class="btn-icon">üìã</text>
					Áº¥Ë¥πËÆ∞ÂΩï
				</view>
				<view class="footer-divider">|</view>
				<view class="footer-btn" @click="unbinding">
					<text class="btn-icon">üîì</text>
