			</view>
		</view>
	</view>
</template>

<style>
.page-container {
	min-height: 100vh;
	background-color: #f5f7fa;
	padding-bottom: 40rpx;
}

.main-content {
	padding: 30rpx;
}

/* ‰ø°ÊÅØÂç°ÁâáÊ†∑Âºè */
.info-card {
	background: #ffffff;
	border-radius: 16rpx;
	padding: 30rpx;
	margin-bottom: 30rpx;
	box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.05);
	transform: translateY(0);
	transition: all 0.3s ease;
}

.info-card-active {
	transform: translateY(0);
	box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.1);
}

.info-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 30rpx;
	padding-bottom: 20rpx;
	border-bottom: 2rpx solid #f5f5f5;
}

.info-title {
	font-size: 32rpx;
