</style>
<script>
	export default {
		data() {
			return {
				amount: '',
				meterInfo: null,
				electricity: null
			};
		},
		onShow() {
			this.queryBalance()
		},
		onLoad(e) {
			this.electricity = e.electricity
			this.queryBalance()
		},
		methods: {
			getorder() {
				uni.navigateTo({
					url:"/pagesTools/electricity/orderlist?electricity="+this.electricity
				})
			},
			// Ëé∑ÂèñÁîµË°®‰ø°ÊÅØ
			queryBalance() {
				var that = this
				that.$util.request("mall.balance", {
					electricity: that.electricity
				}).then(res => {
					if (res.code === 1) {
						res.data.balance = (res.data.balance / 100).toFixed(2)
						this.meterInfo = res.data;
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
					}
				}).catch(err => {
					console.error('Error querying balance:', err);
				});
			},
			//Ëß£Èô§ÁªëÂÆö
			unbinding(){
				var that = this
				uni.showModal({
					confirmText:'Á°ÆÂÆöËß£Èô§',
					confirmColor:"#E60012",
					content:"Á°ÆËÆ§Ëß£Èô§ÁªëÂÆöÊ≠§ÁîµË°®ÂêóÔºü",
					success(res) {
						if(res.confirm){
							that.$util.request("mall.unbinding", {
								electricity: that.electricity
							}).then(res => {
								uni.showToast({
									title: res.msg,
									icon: 'none',
									success() {
										setTimeout(function(){
											uni.navigateBack()
										},2000)
									}
								});
							}).catch(err => {
								console.error('Error querying balance:', err);
							});
						}else{
							console.log("ÂèñÊ∂àËß£Èô§")
						}
					}
				})
			},
			startScan() {
				var that = this
				if (that.amount == '' || that.amount == 0) {
					uni.showToast({
						title: "ËØ∑ËæìÂÖ•ÂÖÖÂÄºÈáëÈ¢ù",
						icon: 'none'
					})
					return false;
				}

				// ËøôÈáåÂèØ‰ª•Â§ÑÁêÜÊâ´ÊèèÂêéÁöÑÈÄªËæëÔºåÊØîÂ¶ÇÊòæÁ§∫ÁîµË°®‰ø°ÊÅØÂíåËæìÂÖ•ÂÖÖÂÄºÈáëÈ¢ùÁ≠â
				that.$util.request("mall.electricity", {
					amount: that.amount,
					electricity: this.electricity
				}).then(res => {
					if (res.code === 200) {
						uni.requestPayment({
							provider: 'wxpay', // ÁúãÈúÄÊ±ÇÔºåÊØè‰∏™Á´ØÈÉΩÊúâÂêÑËá™ÁöÑÂÄºÔºåeg: 'alipay'
							timeStamp: res.data.timeStamp, // ÂΩìÂâçÊó∂Èó¥Êà≥Ôºà‰ªé1970Âπ¥1Êúà1Êó•Ëá≥‰ªäÁöÑÁßíÊï∞Ôºâ
							nonceStr: res.data.nonceStr, // ÈöèÊú∫Â≠óÁ¨¶‰∏≤ - ‰πüÂèØ‰ª•ÂêéÁ´ØËøîÂõû
							package: res.data.package, // ÂêéÁ´ØÊé•Âè£ËøîÂõû
							paySign: res.data.paySign, // ÂêéÁ´ØËøîÂõû
							signType: 'MD5', // Á≠æÂêçÁöÑÁÆóÊ≥ïÔºåÈªòËÆ§ÂÄº ‚ÄôMD5‚Äò
							success: (result) => {
								// resultÊàêÂäüËøîÂõûÂÜÖÂÆπ errMsg: "requestPayment:ok"
								if (result.errMsg == "requestPayment:ok") {
									uni.showToast({
										title: "ÂÖÖÂÄºÊàêÂäü",
										icon: 'none',
										duration: 2000,
										success() {
											setTimeout(() => {
												uni.navigateBack();
											}, 2000);
										}
									})
								}

								// ÊîØ‰ªòÊàêÂäüË∑≥ËΩ¨ÁªìÊûúÈ°µ
							},
							fail: (err) => {
								console.log('fail', err)
								uni.showToast({
									title: 'ËÆ¢ÂçïÂ∑≤ÁîüÊàêÔºåÊîØ‰ªòÂ§±Ë¥•',
									icon: 'none',
									duration: 2000
								})
							}
						})

					} else {
						uni.showToast({
							title: "ËÆ¢ÂçïÁîüÊàêÂ§±Ë¥•",
							icon: 'none',
							duration: 2000
						})
					}
				})

			}
		}
	};
</script>
